<template>
	<div class="QAHead" v-my-directive :data-title="current.name" data-description="学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题">
		<div class="paddingtop10 columnnav Horizontalspacing minixgap fonfff minfont">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item v-for="(item, index) in nav" :key="index" :to="{ path: '/column/' + item.alias + '.html' }" @click="get()">{{ item.name }}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="questionnav line">
			<div class="columnLayout interval">
				<span class="fonfff center QAfont">{{ current.name }}</span>
				<div class="Horizontalspacing interval fonfff">
					<div class="Horizontalspacing minxgap">
						<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
						<div>学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题学习技能、解决问题</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="main">
		<div>
			<div class="content">
				<div class="Genericpadding Horizontalspacing interval">
					<div class="QAgreybox" @click=";(curPage = 1), actr(current.alias)">全部</div>
					<div class="QAgreybox" v-for="(item, index) in subordinate" :key="index" @click=";(curPage = 1), actr(item.alias)">{{ item.name }}</div>
					<!-- <div class="QAgreybox">合约问题</div>
					<div class="QAgreybox">疑难技术</div> -->
				</div>
				<div class="Genericpadding columnLayout">
					<div class="QArectangle">
						<div class="Horizontalspacing interval">
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 0 }" @click=";(tosort = 'time'), (curPage = 1), actr(), (offset = offset === 'DESC' ? 'ASC' : 'DESC')">最新文章</div>
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 1 }" @click=";(tosort = 'time'), (curPage = 1), actr(), (offset = offset === 'DESC' ? 'ASC' : 'DESC')">按照時間</div>
							<div class="qzsolld" :class="{ qzsolldav: tesorting == 2 }" @click=";(tosort = 'popularity'), (curPage = 1), actr(), (offset = offset === 'DESC' ? 'ASC' : 'DESC')">按照人氣</div>
						</div>
					</div>
					<ul class="columnLayout">
						<router-link v-if="arclist" v-for="(item, index) in arclist" :key="index" :to="'/article/' + item.alias + '/' + item.id">
							<li class="Genericpadding Horizontalspacing interval">
								<div>
									<el-image class="PostThumbnails" :src="item.thumbnail ? item.thumbnail : ''" lazy>
										<template #error>
											<div class="image-slot iconfont icon-jiazaitupian"></div>
										</template>
									</el-image>
								</div>
								<div class="ArticleList">
									<div class="Horizontalspacing minxgap ArticleTitlep">
										<div class="smallsquare bold"><span class="center">热门</span></div>
										<span class="turngreen Widthlimit">{{ item.title }}</span>
									</div>
									<span class="minfont grey Widthlimits">{{ item.description }}</span>
									<div class="articlebt minfont">
										<div class="Horizontalspacing interval">
											<div class="Horizontalspacing minixgap">
												<el-avatar class="pimgmins" :src="item.user.Avatar" :size="60">
													<div class="image-slot iconfont icon-setting-role"></div>
												</el-avatar>
												<span v-if="item.user.nickname" class="center">{{ item.user.nickname }}</span>
												<span v-else class="center">{{ item.user.username }}</span>
											</div>
											<div class="grey center">{{ item.pidname }}</div>
										</div>
										<div class="Horizontalspacing interval grey">
											<div class="Horizontalspacing minixgap">
												<span class="center">日期:</span>
												<span class="center">{{ item.Creationtime }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">点赞:</span>
												<span class="center">{{ item.Like.length }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">浏览:</span>
												<span class="center">{{ item.PageView }}</span>
											</div>
										</div>
									</div>
								</div>
							</li>
						</router-link>
						<div v-else>
							<el-skeleton v-for="index in pagesize" :key="index" class="Genericpadding Horizontalspacing interval navwidth" animated>
								<template #template>
									<div>
										<el-skeleton-item variant="image" style="width: 200px; height: 150px" />
									</div>
									<div class="ArticleList">
										<div>
											<el-skeleton :rows="3" style="width: 597px" animated />
										</div>
										<div class="articlebt minfont">
											<el-skeleton-item variant="h3" style="width: 154px" />
											<el-skeleton-item variant="h3" style="width: 319px" />
										</div>
									</div>
								</template>
							</el-skeleton>
						</div>
					</ul>
					<el-pagination background class="centerend" :hide-on-single-page="value" @current-change="fun" :current-page="curPage" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
				</div>
			</div>
		</div>
		<div>
			<left></left>
		</div>
	</div>
</template>
<script>
import left from './../components/left'
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		left,
	},
	data() {
		return {
			comn: this.$route.params.comn.replace(/.html/g, ''),
			arclist: '',
			nav: [],
			current: [],
			subordinate: [],

			qzsolldav: `qzsolldav`,
			tesorting: 0,
			order: false,

			//返回值
			value: true,
			pagesize: 20, //每页显示数量
			curPage: 1, // 当前页数
			total: 0, // 一共几条数据
			offset: 'DESC',
			tosort: 'time',
		}
	},
	methods: {
		//默认 函数
		async get() {
			console.log(this.comn)
			let comn = await api(['post=Obtainclassification', this.comn])
			this.nav = comn.data

			//获取下级分类
			let comnlist = await api(['post=Getification', this.comn])

			this.current = comnlist['data'][0]
			this.subordinate = comnlist['data'][1]
			this.actr()
		},
		// 获取文章
		// 如果为空
		async actr(ele = '') {
			this.arclist = ''

			if (ele) {
				this.comn = ele
			}
			let array = await api(['post=getarticlepage', { perPage: this.pagesize, page: this.curPage, offset: this.offset, tosort: this.tosort, alias: this.comn }])
			console.log(array)
			if (array.code == 200) {
				this.total = array.massage / 1
				array = array.data
				this.arclist = array
				// this.Updatesorting()
			} else {
				this.total = 0
			}
		},
		// 分页
		async fun(ele = 1) {
			window.scrollTo({ behavior: 'instant', top: 400 })
			this.curPage = ele
			await this.actr()
			let arr = []
			for (let i = this.index; i < this.arclist.length; i++) {
				arr.push(this.arclist[i].id)
			}

			api(['post=acbrowsing', arr])
		},
	},

	mounted() {
		// 生命周期:
		this.get()
	},
	updated() {
		// this.loading = false
	},
}
</script>
<style scoped>
.image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 200px;
	height: 150px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.image-slot {
	font-size: 30px;
}
.qzsolldav {
	color: var(--primary-color);
}
.Widthlimit {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 560px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.Widthlimits {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 597px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
</style>
